import { cookies } from "next/headers";
import { redirect } from "next/navigation";
import { FacultyDashboardTabs } from "./FacultyDashboardTabs";
import { getMockDb } from "@/lib/mockDb";

export default async function FacultyDashboardPage() {
  const cookieStore = cookies();
  const userId = cookieStore.get("auth_user_id")?.value;
  const userRole = cookieStore.get("auth_role")?.value;

  if (!userId || userRole !== "faculty") {
    redirect("/login");
  }

  const db = getMockDb();
  const facultyUser =
    db.faculty.find((user) => user.id === userId) ?? db.faculty[0];

  if (!facultyUser) {
    redirect("/login");
  }

  return (
    <FacultyDashboardTabs
      stats={facultyUser.dashboard.stats}
      facultyMembers={facultyUser.dashboard.facultyMembers}
      facultyName={facultyUser.name}
    />
  );
}
